import React, { Component } from 'react';
import './header.css';
import { Link } from 'react-router-dom';

export default class Header extends Component {
    constructor() {
      super()
      this.state = {
        isActive: false
      }
    }
    toggleActive() {
      this.setState({
        isActive: !this.state.isActive
      });
      const mobileMenuWrapper = document.querySelector('.mobile-menu-wrapper');
      mobileMenuWrapper.classList.contains('active') ? document.body.style.overflow = 'auto' :  document.body.style.overflow = 'hidden';
    }
    render() {
        const {isActive} = this.state;
         return (
            <div className="header">
                <main id="main">
                    <section id="section-header">
                        <div className="header">
                            <div className="header-inner">
                                <div className="photo-grid">
                                    <div className="shadow"></div>
                                    <div className="hashtag">
                                        <a href="#">#hotdogs</a>
                                    </div>
                                    <div className="grid">
                                        <img className="photo" src="img/photo1.png" alt="hotdog"></img>
                                        <img className="photo" src="img/photo2.png" alt="hotdog"></img>
                                        <img className="photo" src="img/photo3.png" alt="hotdog"></img>
                                        <img className="photo" src="img/photo4.png" alt="hotdog"></img>
                                        <img className="photo" src="img/photo5.png" alt="hotdog"></img>
                                        <img className="photo" src="img/photo6.png" alt="hotdog"></img>
                                    </div>
                                    <div className="grid">
                                        <img className="photo" src="img/photo7.png" alt="hotdog"></img>
                                        <img className="photo" src="img/photo8.png" alt="hotdog"></img>
                                        <img className="photo" src="img/photo9.png" alt="hotdog"></img>
                                        <img className="photo" src="img/photo10.png" alt="hotdog"></img>
                                        <img className="photo" src="img/photo11.png" alt="hotdog"></img>
                                        <img className="photo" src="img/photo12.png" alt="hotdog"></img>
                                    </div>
                                </div>
                                <nav className="desctop-menu">
                                    <ul>
                                        <Link to="/">
                                            <li>Menu</li>
                                        </Link>
                                        <Link to="/Catering">
                                            <li>Catering</li>
                                        </Link>
                                        <Link to="/AboutUs">
                                            <li>About us</li>
                                        </Link>
                                        <Link to="/Contacts">
                                            <li>Contact</li>
                                        </Link>
                                    </ul>
                                </nav>
                            </div>
                            <div className="mobile-controls" onClick={()=>this.toggleActive()}>
                                <div className={`hotdog ${isActive ? 'active' : '' }`}>
                                <img src="icons/hotdog-mobile.png" alt="hotdog"></img>
                                </div>
                                <div className={`close ${isActive ? 'active' : '' }`}>
                                <img src="icons/close.png" alt="close"></img>
                                </div>
                            </div>
                            <nav className={`mobile-menu-wrapper ${isActive ? 'active' : '' }`}>
                                <div className="mobile-menu">
                                    <ul>
                                        <Link to="/">
                                            <li>Menu</li>
                                        </Link>
                                        <Link to="/Catering">
                                            <li>Catering</li>
                                        </Link>
                                        <Link to="/AboutUs">
                                            <li>About us</li>
                                        </Link>
                                        <Link to="/Contacts">
                                            <li>Contact</li>
                                        </Link>
                                    </ul>
                                </div>
                            </nav>
                        </div>
                    </section>
                </main>
            </div>
        );
    }
}

