import React, { Component } from 'react';
import axios from 'axios';
import './contacts.css';

export default class Contact extends Component {
    constructor() {
      super()
      this.state = {
        name: '',
        email: '',
        comment: '',
      }
    }
    changeHandler = e => {
      this.setState({[e.target.name]: e.target.value});
    }
    componentDidMount() {
      // axios.post(' https://formula-test-api.herokuapp.com/contact').then(res => {
  
      // });
    }
    submitHandler = e => {
        e.preventDefault();
        axios.post(' https://formula-test-api.herokuapp.com/contact', this.state).then(result => {
            console.log(result);
            console.log(result.data);
        });
    }
    render() {
        const { name, email, comment } = this.state
        return (
            <div className="contacts">
                <section id="contacts">
                    <div className="form-wr">
                        <form onSubmit={this.submitHandler}>
                            <div className="form-inner">
                                <div className="inputs-wr">
                                    <input type="text" name="name" placeholder="Your Name" id="name" value={name} onChange={this.changeHandler}></input>
                                    <input type="email" name="email" placeholder="Email Address" id="e-mail" value={email} onChange={this.changeHandler}></input>
                                </div>
                                <textarea name="comment" id="message" placeholder="Comment..." maxLength="1000" value={comment} onChange={this.changeHandler}></textarea>
                                <div className="button">
                                    <button>Send Message</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </section>
            </div>
        );
    }
}

