import React, { Component } from 'react';
import axios from 'axios';
import './content.css';

export default class Content extends Component {
  state = {
    menuItems: [],
  };
  componentDidMount() {
    axios.get('https://formula-test-api.herokuapp.com/menu').then(result => {
      const items = result.data;
      // const today = new Date();
      const today = new Date('01/01/2018');  // 2018 YEAR !!!
      const arrFromRequest = items.filter((item) => {
          const elemPart = item.expirationDate.split('-');
          const elemDate = new Date(elemPart[2], +elemPart[0] - 1, elemPart[1]);
          return elemDate > today;
      });
      this.setState({ menuItems: arrFromRequest });
    });
  }
  render() {
    return (
      <div className="content">
        <section id="section-content">
          <div className="content">
              <div className="intro">
                <div className="intro-icon">
                  <img src="icons/hotdog.png" alt="hotdog"></img>
                </div>
                <h2>Dirty Dogs serves all-beef, vegan and
                  vegatagian hot dogs.</h2>
                <div className="btn">
                  <a href="#">More Dogsâ€˜n Make Em Hot</a>
                </div>
              </div>
              <div className="content-wr">
                {this.state.menuItems.map(menuItem => 
                  <div className="column">
                    <div className="block">
                      <div className="block-text">
                          <div className="block-text-inner">
                            <div className="block-title">
                              <h2>{menuItem.name}</h2>
                            </div>
                            <div className="block-content">
                              <p>
                                {menuItem.description}
                              </p>
                            </div>
                          </div>
                      </div>
                    </div>
                    <div className="block">
                      <div className="block-image">
                          <img src={menuItem.backgroundURL} alt="image"></img>
                      </div>
                    </div>
                  </div>
                )}
              </div>
          </div>
        </section>

      </div>
    );
  }
}
